@use "../utils/functions/"as *;
@use "../utils/mixins"as *;

.corruption {
  &__list {
    margin-bottom: 2rem;

    li {
      @include flex-v-center;

      &::before {
        content: "-";
        padding-inline: 1ch;
      }

      &:not(:last-child) {
        margin-bottom: 1rem;
      }
    }

    a {
      text-decoration: underline;
      transition: color 0.3s linear;

      &:hover {
        color: var(--color-yellow-200);
      }
    }
  }

  h1+p {
    margin-bottom: 2rem;
    font-weight: 900;
  }

  .btn--corruption {
    margin: 0 auto 2rem;
  }
}
