@use "../../utils/functions" as *;
@use "../../utils/mixins" as *;

.calculator {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 30px;

  @include media(desktop) {
    gap: 15px;
  }

  @include media(tablet) {
    flex-wrap: wrap;
    gap: 15px;
  }

  &__container {
    @include flex-col;
    min-height: 660px;
    max-width: 930px;
    width: 100%;
  }

  &__tabpanel:nth-child(2) {
    h2 {
      margin-bottom: 30px;
    }
  }

  h2 {
    font-size: 18px;
  }

  &__list--submit,
  &__container {
    border: 1px solid var(--color-white);
    border-radius: var(--radius-big);
    padding: 60px;

    @include media(desktop-wide) {
      padding: 40px 25px;
    }

    @include media(tablet) {
      padding: 30px 20px;
    }

    @include media(mobile-m) {
      padding: 30px 15px;
    }
  }

  &__button-panel {
    display: flex;
    gap: rem(40);
    margin-top: auto;

    @include media(mobile-s) {
      flex-wrap: wrap;
    }
  }

  &__buttons {
    display: flex;
    gap: 15px;
  }

  &__button button{
    width: 50px;
    height: 50px;
    background-color: var(--color-blue-400);
    color: var(--color-gray-100);
    border-radius: var(--radius-round);
    transition-property: background-color, color;
    transition-duration: .3s;
    transition-timing-function: linear;

    &:hover {
      background-color: var(--color-gray-100);
      color: var(--color-white);

      @include media(tablet) {
        background-color: var(--color-blue-400);
        color: var(--color-gray-100);
      }
    }
  }

  &__button button[aria-selected="true"]{
    background-color: var(--color-orange-400);
    color: var(--color-white);
  }

  &__list {
    display: grid;
    width: 100%;
    gap: rem(43) rem(25);

    @include media(tablet) {
      align-items: end;
    }

    @include media(mobile-s) {
      gap: rem(43) 0;
    }

    &--submit {
      flex: 1 1 450px;
      .btn {
        max-width: 100%;
      }
    }

    &:not(.calculator__list--submit) {
      margin-bottom: 50px;
    }
  }

  &__list--first {
    grid-template-columns: repeat(4, 1fr);

    @include media(desktop-wide) {
      grid-template-columns: repeat(2, 1fr);
    }

    @include media(tablet) {
      grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    }

    @include media(mobile-s) {
      grid-template-columns: 1fr;
    }
  }

  &__list--first>.calculator__item:first-child {
    grid-column: 1/3;

    @include media(desktop) {
      grid-column: 1/4;
    }

    @include media(tablet) {
      grid-column: 1/2;
    }
  }

  &__list--first>.calculator__item:nth-child(2) {
    grid-column: 3/6;

    @include media(desktop) {
      grid-column: 1/4;
    }

    @include media(tablet) {
      grid-column: 2/3;

      label {
        white-space: wrap;
      }
    }
  }

  &__list--first>.calculator__item:nth-child(3) {

    @include media(desktop) {
      grid-column: 1/2;
    }
  }

  &__list--first>.calculator__item:nth-child(4) {

    @include media(desktop) {
      grid-column: 2/3;
    }
  }

  &__list--first>.calculator__item:nth-child(5) {
    grid-column: 3/6;

    @include media(desktop) {
      grid-column: 1/3;
    }
  }

  &__list--first>.calculator__item:nth-child(6) {
    grid-column: 1/3;

    @include media(desktop) {
      grid-column: 1/3;
    }
  }

  &__list--first>.calculator__item:nth-child(7) {
    grid-column: 3/6;

    @include media(desktop) {
      grid-column: 1/3;
    }
  }

  &__list--first>.calculator__item:first-child,
  &__list--first>.calculator__item:nth-child(2),
  &__list--first>.calculator__item:nth-child(3),
  &__list--first>.calculator__item:nth-child(4),
  &__list--first>.calculator__item:nth-child(5),
  &__list--first>.calculator__item:nth-child(7)
  &__list--first>.calculator__item:nth-child(6) {

    @include media(mobile-l) {
      grid-column: 1/2;
    }
  }

  &__list--third {
    grid-template-columns: repeat(3, 1fr);

    @include media(desktop) {
      grid-template-columns: 2fr;
    }
  }

  &__list--third>.calculator__item:first-child {
    grid-column: 1/5;

    @include media(desktop) {
      grid-column: 1/3;
    }
  }

  &__list--third>.calculator__item:nth-child(2) {
    grid-column: 5/6;

    @include media(desktop) {
      grid-column: 1/2;
    }
  }

  &__list--third>.calculator__item:nth-child(3) {
    grid-column: 1/2;

    @include media(desktop) {
      grid-column: 2/3;
    }
  }

  &__list--third>.calculator__item:nth-child(4) {
    grid-column: 2/4;

    @include media(desktop) {
      grid-column: 1/2;
    }
  }

  &__list--third>.calculator__item:nth-child(5) {
    grid-column: 4/6;

    @include media(desktop) {
      grid-column: 2/3;
    }
  }

  &__list--third>.calculator__item:first-child,
  &__list--third>.calculator__item:nth-child(2),
  &__list--third>.calculator__item:nth-child(3),
  &__list--third>.calculator__item:nth-child(4),
  &__list--third>.calculator__item:nth-child(5) {

    @include media(mobile-l) {
      grid-column: 1/2;
    }
  }

  fieldset {
    border: none;
    max-width: 500px;
  }

  legend {
    margin-bottom: 20px;
  }

  fieldset>div {
    display: flex;
    justify-content: space-between;
    gap: 20px;

    @include media(mobile-m) {
      align-items: flex-start;
      flex-direction: column;
    }
  }

  &__label,
  h3 {
    font-size: 18px;
    font-weight: var(--fw-700);
  }

  &__label {
    position: relative;
    @include flex-col;
    gap: 20px;
    white-space: nowrap;

    @include media(mobile-s) {
      white-space: wrap;
    }

    &--checkbox {
      width: 100%;
      white-space: nowrap;
    }

    &--radio {
      flex-direction: row-reverse;
      cursor: pointer;
      align-items: center;
      font-weight: var(--fw-500);
      color: var(--color-gray-100);
      transition-property: color;
      transition-duration: .3s;

      &:hover {
        color: var(--color-white);

        @include media(tablet) {
          color: var(--color-gray-100);
        }
      }

      &:has(input[type="radio"]:checked) {
        color: var(--color-white);
      }
    }
  }

  &__checker {
    position: relative;
    align-items: center;
    max-width: 180px;
		width: 100%;
    height: 70px;
    background-color: transparent;
    border: 1px solid var(--color-gray-100);
		cursor: pointer;
		border-radius: calc(var(--radius-big) * 5);

    @include media(mobile-s) {
      height: 50px;
    }

    & span {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 1;
      font-weight: var(--fw-500);
      transition-property: color, transform;
      transition-duration: .3s;
      transition-timing-function: ease;

      &:first-child {
        left: 30px;
      }

      &:last-child {
        right: 25px;
        color: var(--color-gray-100);

        &:hover {
          color: var(--color-white);

          @include media(tablet) {
            color: var(--color-gray-100);
          }
        }
      }
    }

    &::after {
      content: "";
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      transform: translateX(0);
      max-width: 85px;
      width: 100%;
      height: 100%;
      border-radius: calc(var(--radius-big) * 5);
      background-color: var(--color-orange-400);
      transition: all .4s ease;
    }
  }

  input[type="checkbox"],
  input[type="radio"] {
    position: absolute;
  }

  input[type="checkbox"]:checked + .calculator__checker::after {
    left: auto;
    right: 85px;
    transform: translateX(100%);
    transition: all .4s ease;
  }

  input[type="checkbox"]:checked + .calculator__checker span:last-child {
    color: var(--color-white);
  }

  input[type="checkbox"]:checked + .calculator__checker span:first-child {
    color: var(--color-gray-100);

    &:hover {
      color: var(--color-white);

      @include media(tablet) {
        color: var(--color-gray-100);
      }
    }
  }

  &__radio {
    position: relative;
    display: inline-block;
    width: 20px;
    height: 20px;
    background-color: var(--color-gray-100);
    border-radius: var(--radius-round);
  }

  input[type="radio"]:checked + .calculator__radio {
    background-color: var(--color-white);

    &::before {
      content: "";
      position: absolute;
      width: 12px;
      height: 12px;
      background-color: var(--color-orange-400);
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      border-radius: var(--radius-round);
    }
  }

  .choices {
    max-width: 390px;
    width: 100%;
    margin-top: auto;

    @include media(tablet) {
      max-width: 100%;
    }

    & .choices__inner {
      padding-bottom: 25px;
    }

    &:hover {
      &:after {
        background-color: var(--color-white);

        @include media(tablet) {
          background-color: var(--color-gray-100);
        }
      }

      .choices__placeholder {
        color: var(--color-white);

        @include media(tablet) {
          color: var(--color-gray-100);
        }
      }
    }

    &::after {
      height: 15px;
      width: 15px;
      @include mask;
      mask-image: url("../images/icons/arrow.svg");
      position: absolute;
      background-color: var(--color-gray-100);
      top: 50%;
      transform: translateY(-50%) rotate(90deg);
      border: none;
      margin-top: 0;
      right: 30px;
    }

    &__inner {
      background-color: transparent;
      color:  var(--color-gray-100);
      border-color: var(--color-gray-100);
      border-radius: var(--radius-main);
      font-size: 15px;
      font-weight: var(--fw-500);
      padding: 25px;

      @include media(tablet) {
        padding: 15px;
      }
    }

    &__list--dropdown {
      background-color: var(--color-dark-900);
      font-size: 15px;
      font-weight: var(--fw-500);
      z-index: 2;

      & .choices__item--selectable {
        padding: 30px;
        transition-property: background-color;
        transition-duration: .3s;
      }

      & .choices__item--selectable:focus {
        background-color: var(--color-orange-400);
      }

      & .choices__item--selectable.is-selected{
        background-color: var(--color-orange-400);
      }

      & .choices__item--selectable.is-highlighted:not(.is-selected){
        background-color: var(--color-blue-400);
      }

      & .choices__item--selectable.is-highlighted:after {
        display: none;
      }
    }

    &__placeholder {
      color: var(--color-gray-100);
      transition-property: color;
      transition-duration: .3s;
      transition-timing-function: linear;
    }
  }

  .is-open {
    &:after {
      transform: rotate(270deg);
      margin-top: 0;
    }
  }

  &__total-sum {
    color: var(--color-gray-100);
  }

  &__total-price {
    font-size: 30px;
    font-weight: var(--fw-700);
  }

  &__price-counter {
    &::before {
      content: "$";
    }
  }

  &__table {
    height: 370px;
    width: 100%;
    overflow-y: scroll;
    @include flex-col;
    font-size: 15px;
    gap: 20px;
    padding-right: 60px;

    @include media(mobile-l) {
      padding-right: 20px;
    }

    @include media(mobile-m) {
      padding-right: 10px;
    }

    .calculator__label--checkbox {
      max-width: 90px;
    }

    .calculator__checker {
      height: 30px;

      &::after {
        content: "";
        max-width: 45px;
      }

      & span {
        font-size: 15px;

        &:first-child {
          left: 12px;
        }

        &:last-child {
          right: 10px;
          color: var(--color-gray-100);
        }
      }
    }

    input[type="checkbox"]:checked + .calculator__checker::after {
      right: 45px;
    }
  }

  &__table-row {
    width: 100%;
    border: 1px solid var(--color-blue-400);
    border-radius: var(--radius-main);


    &:nth-child(even) {
      background-color: var(--color-blue-400);
      border: 1px solid var(--color-dark-900);

      & .calculator__row-item {
        border-right: 1px solid var(--color-dark-900);
      }
    }

    &:nth-child(odd) {
      & .calculator__row-item:not(.calculator__row-item:last-child) {
        border-right: 1px solid var(--color-blue-400);
      }
    }
  }

  &__row-list {
    display: grid;
    grid-template-columns: 3fr 1fr 1.5fr;
    justify-content: center;

    @include media(mobile-m) {
      grid-template-columns: 2fr 1fr 2fr;
    }
  }

  &__row-item {
    padding: 10px;
    height: 100%;
    display: flex;
    align-items: center;
  }

  &__row-item:not(.calculator__row-item:first-child) {
    justify-content: center;
  }

  .link--prev {
    &::after {
      display: none;
    }

    &::before {
      @include mask;
      mask-image: url("../images/icons/arrow.svg");
      transform: rotate(180deg);
    }
  }
}