@use "../utils/mixins" as *;
@use "../utils/functions" as *;

.contacts {
  h1 + p {
    margin-bottom: 30px;
  }

  &__list {
    display: grid;
    margin-bottom: 3rem;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;

    @include media(tablet-wide) {
      justify-content: center;
      flex-wrap: wrap;
    }

    @include media(mobile-s) {
      flex-direction: column;
      align-items: center;
    }
  }

  &__item {
    @include flex-col;
    gap: 10px;
  }

  &__name {
    @include flex-v-center;

    font-weight: var(--fw-600);
    font-size: 20px;
    gap: 15px;

    &::before {
      @include mask;

      mask-image: url("../images/icons/phone.svg");
      width: 24px;
      height: 24px;
    }

    &--email {
      &::before {
        mask-image: url("../images/icons/email.svg");
      }
    }

    &--phone {
      &::before {
        mask-image: url("../images/icons/phone.svg");
      }
    }

    &--schedule {
      &::before {
        mask-image: url("../images/icons/schedule.svg");
      }
    }

    &--address {
      &::before {
        mask-image: url("../images/icons/pin.svg");
      }
    }

    @include media(mobile-s) {
      justify-content: center;
    }
  }

  &__link {
    font-size: clamp(16px, 0.8942rem + 0.1923vw, 18px);
    transition: color 0.3s linear;

    &:hover:not(.contacts__link--address, .contacts__link--schedule) {
      color: var(--color-orange-400);
    }
  }

  &__img {
    img {
      min-height: 550px;
    }
  }
}
