@use "../utils/functions/" as *;
@use "../utils/mixins" as *;

.modal {
  margin: auto;
  border: 0;
  border-radius: var(--radius-big);
  padding: clamp(16px, 2.5vw, 50px) clamp(16px, 7.8vw, 150px);
  width: min(90%, 990px);
  color: var(--color-white);
  background-color: var(--color-brown-400);

  &::backdrop {
    background: rgba(0, 19, 34, 0.7);
  }

  &__title {
    margin-bottom: 2.5rem;
    width: 100%;
    text-align: center;

    + p {
      margin-bottom: 30px;
      text-align: center;
    }
  }

  &__close:not(.btn) {
    @include flex-all-center;

    position: absolute;
    right: clamp(10px, 2vw, 50px);
    top: clamp(10px, 2vw, 50px);

    &::before {
      @include mask;

      mask-image: url("../images/icons/close.svg");
      width: 25px;
      height: 25px;
      background-color: var(--color-white);
    }

    &:hover {
      &::before {
        background-color: var(--color-orange-400);
      }
    }
  }

  .btn {
    max-width: 100%;
    color: var(--color-gray-10);
    border-color: var(--color-gray-10);
  }

  .form__input {
    color: var(--color-white);
  }

  &--finish {
    padding: clamp(16px, 10.67vw, 205px) clamp(16px, 11.7vw, 225px);

    .title {
      @include flex-all-center;
      gap: 50px;

      &::before {
        @include mask;
        mask-image: url("../images/icons/done.svg");
        width: 70px;
        height: 70px;
      }
    }
  }

  &[open] {
    animation:
      slide-up 0.6s forwards,
      fade-in 0.6s forwards;
  }

  @include media(mobile-l) {
    .title {
      gap: 20px;
      
      &::before {
        width: 40px;
        height: 40px;
      }
    }
  }
}
