@use "../utils/mixins" as *;
@use "../utils/functions" as *;

.single {
  &__category {
    margin-bottom: 30px;

    span {
      font-size: 15px;
      line-height: 1.5;
      color: var(--color-gray-100);
    }

    div {
      @include flex;
      gap: 30px;

      span {
        @include flex-v-center;
        color: var(--color-white);
        gap: 10px;

        &::before {
          @include mask;
          display: block;
          mask-image: url("../images/icons/icon-calendar.svg");
          width: 25px;
          height: 25px;
        }

        &:nth-child(2) {
          &::before {
            mask-image: url("../images/icons/icon-clock.svg");
          }
        }

        &:nth-child(4) {
          &::before {
            mask-image: url("../images/icons/icon-view.svg");
          }
        }
      }
    }

    &__categories {
      @include flex-v-center;

      flex-wrap: wrap;
      margin-bottom: 10px;
      gap: 30px;

      span {
        @include flex-v-center;
        gap: 10px;

        &::before {
          @include mask;
          display: block;
          mask-image: url("../images/icons/icon-calendar.svg");
          width: 25px;
          height: 25px;
        }

        &:first-child {
          color: var(--color-gray-100);

          &::before {
            content: none;
          }
        }

        &:nth-child(3) {
          &::before {
            mask-image: url("../images/icons/icon-clock.svg");
          }
        }

        &:nth-child(4) {
          &::before {
            mask-image: url("../images/icons/icon-view.svg");
          }
        }
      }
    }
  }

  &__desc {
    margin-bottom: 2.6rem;
  }

  &__wrapper {
    @include flex-sb;

    gap: 30px;
  }

  &__content {
    width: min(80%, 930px);
  }

  &__popular {
    width: min(80%, 330px);
  }

  &__grid {
    @include flex-col;
    gap: 30px;
  }

  .link--back {
    margin-bottom: 50px;
  }

  h2,
  h3 {
    margin-bottom: 10px;
    font-size: clamp(18px, 0.9519rem + 0.7692vw, 30px);
  }

  img {
    overflow: hidden;
    border-radius: var(--radius-big);

    &:not(:last-child) {
      margin-bottom: 2.6rem;
    }
  }

  @include media(tablet) {
    &__wrapper {
      flex-direction: column;
    }

    &__content {
      width: 100%;
    }

    &__popular {
      width: 100%;

      h2 {
        width: 100%;
        text-align: center;
      }
    }

    &__grid {
      flex-direction: row;
      justify-content: center;
    }
  }

  @include media(mobile-s) {
    &__grid {
      flex-direction: column;
    }
  }
}
