@use "../utils/functions/" as *;
@use "../utils/mixins/" as *;

.header {
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  z-index: 20;
  margin: 0 auto;
  width: 100%;
  transition: all 0.3s ease;

  &__wrapper {
    @include flex-all-center;
  }

  &.show,
  .header__top {
    opacity: 1;
    transition:
      transform 0.3s,
      opacity 0.3s;
  }

  &.scroll {
    .header__bottom {
      position: fixed;
      z-index: 20;
      left: 0;
      right: 0;
      top: 0;
      width: 100%;

      background-color: var(--color-blue-200);
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.4);

      .social {
        display: none;
      }
    }
  }

  .nav {
    background-color: var(--color-blue-200);
  }

  .social {
    width: 100%;
    max-width: 270px;
    padding: 15px;
    background-color: var(--color-yellow-200);

    &__link {
      border-color: var(--color-blue-200);

      &::before {
        background-color: var(--color-blue-200);
      }

      &:hover {
        &::before {
          background-color: var(--color-dark-900);
        }
      }
    }
  }





  &__top {
    @include cover-background;
    padding: 10px 0;
    margin-bottom: 20px;
    transition:
      transform 0.3s ease,
      opacity 0.3s ease;
    will-change: transform, opacity;

    &.hide {
      transform: translateY(-100%);
      opacity: 0;
    }

    &.show {
      animation: fadeInDown 0.6s ease forwards;
    }
  }

  &__bottom {
    @include flex-all-sb;
    margin-bottom: 20px;
  }

  &__inner {
    @include flex-all-sb;
    gap: 1rem;
  }

  &__desc {
    font-weight: 700;
    font-size: clamp(20px, 0.9602rem + 1.3636cqi, 35px);
    color: var(--color-white);
    text-transform: uppercase;
    text-align: center;
    text-shadow:
      -1px 0 #001322,
      0 -1px #001322,
      1px 0 #001322,
      0 1px #001322,
      -1px -1px #001322,
      1px 1px #001322,
      -1px 1px #001322,
      1px -1px #001322,
      1px 0 4px #001322,
      0 0 2px #001322,
      0 0 3px #001322,
      0 0 4px #001322;
  }
}

@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translateY(-10%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
