@use "../utils/functions/"as *;
@use "../utils/mixins/"as *;

.footer {
  background-color: var(--color-dark-900);
  padding-block: 2rem;

  &__inner {
    @include flex-all-sb;

    flex-wrap: wrap;
    gap: 30px;
  }

  &__copyright {
    @include flex-all-sb;

    font-size: clamp(18px, 0.9227rem + 0.3636vw, 20px);
    letter-spacing: 0.04em;
    color: var(--color-white);
    gap: 30px;

    span:first-of-type {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        right: -15px;
        width: 1px;
        height: 100%;
        background-color: var(--color-white);
      }
    }
  }
}