/* stylelint-disable declaration-no-important */
@use "../utils/functions/" as *;
@use "../utils/mixins" as *;

.link {
  display: inline-flex;
  align-items: center;
  position: relative;
  font-size: clamp(16px, calc(0.975rem + 0.125vw), 18px);
  white-space: nowrap;
  text-decoration: underline;
  text-decoration-color: transparent;
  color: var(--color-orange-400);
  transition: text-decoration-color 0.3s linear;
  gap: 15px;

  &::after,
  &::before {
    @include mask;

    mask-image: url("../images/icons/arrow.svg");
    width: 16px;
    height: 16px;
    transition:
      transform 0.3s linear,
      color 0.3s linear;
  }

  &::before {
    content: none;
  }

  &:hover {
    &::after,
    &::before {
      transform: translateX(5px);
    }
  }

  &--view {
    &::before {
      content: "";
      left: auto;
      mask-image: url("../images/icons/pdf.svg");
      width: 24px;
      height: 24px;
      transition: transform 0.5s linear;
    }

    &::after {
      content: none;
    }
  }

  &--download {
    &::after {
      content: "";
      left: auto;
      mask-image: url("../images/icons/pdf.svg");
      width: 24px;
      height: 24px;
      transition: transform 0.5s linear;
    }

    &::before {
      content: none;
    }
  }

  &--back {
    &::before {
      content: "";
      rotate: 180deg;
    }

    &::after {
      content: none;
    }

    &:hover {
      color: var(--color-orange-400);

      &::before {
        transform: translateX(5px);
      }
    }
  }
}
