@use "../utils/functions/"as *;
@use "../utils/mixins/"as *;

.nav {
  @include flex-all-sb;
  padding: 18px 0;
  height: 70px;
  position: relative;
  width: 100%;

  &__inner {
    @include flex-v-center;

    width: 100%;
  }

  &__list {
    @include flex-all-center;

    margin: 0 auto;

    li {
      &:not(:last-child) {
        margin-right: clamp(20px, 2.61cqi, 50px);
      }

      .sub-menu {
        position: absolute;
        top: 0;
        left: 100%;
        z-index: 10;
        display: none;
        box-shadow: 0 0 2px var(--color-dark-900);
        width: 305px;
        background-color: var(--color-white);
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.3s linear;

        li {
          margin-right: 0;

          &:not(:last-child) {
            margin-bottom: 10px;
          }
        }

        a {

          &::after {
            content: none;
          }
        }
      }

      &:hover {
        .sub-menu {
          display: flex;
          flex-direction: column;
          opacity: 1;
          visibility: visible;
        }
      }
    }

    a {
      position: relative;
      font-size: 18px;
      text-transform: uppercase;
      white-space: nowrap;
      transition: color 0.3s ease;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: -12px;
        width: 100%;
        height: 2px;
        background-color: currentcolor;
        transform: scale(0);
        transform-origin: left;
        transition: transform 0.3s linear;
      }

      &:hover,
      &.true {
        color: var(--color-yellow-200);

        &::before {
          transform: scale(1);
        }
      }
     
    }

    .link-dropdown {
      @include flex-all-center;

      gap: 10px;

      &::after {
        @include mask;

        mask-image: url("../images/icons/chevron.svg");
        rotate: -90deg;
        width: 12px;
        height: 8px;
        transition: transform 0.3s linear;
      }

      &:has(+ .sub-menu.show) {
        &::after {
          transform: scaleY(1);
        }
      }
    }
  }

  // @include media(tablet-wide) {
  //   li {
  //     &:not(:last-child) {
  //       margin-right: 20px;
  //     }
  //   }
  // }

  @include media(tablet) {
    &__inner {
      position: absolute;
      right: calc(var(--container-offset) * -1);
      top: calc(var(--header-height) / 2.01);
      z-index: -1;
      flex-direction: column;
      justify-content: flex-start;
      overflow-y: auto;
      padding: 40px 0;
      width: 100%;
      height: calc(var(--vh) - var(--header-height) * 0.4);
      max-width: 350px;
      background-color: var(--color-dark-900);
      transform: translateX(150%);
      transition-property: transform;
      transition-duration: 0.25s;
    }

    &__list {
      flex-direction: column;
      margin: 0;

      li {
        &:not(:last-child) {
          margin-right: 0;
        }

        .sub-menu {
          position: relative;
          left: 0;

          &.show {
            display: flex;
            flex-direction: column;
            opacity: 1;
            visibility: visible;
          }
        }
      }

      .link-dropdown {
        &:has(+ .sub-menu.show) {
          &::after {
            rotate: -180deg;
          }
        }
      }
    }

    &__link {
      font-size: 18px;
    }
  }
}