@use "../utils/functions/" as *;
@use "../utils/mixins/" as *;

h1,
h2,
h3 {
  overflow: hidden;
  width: fit-content;
  letter-spacing: 0.03em;
}

h1 {
  font-weight: var(--fw-900);
  font-size: clamp(45px, 0.6408rem + 7.2785vw, 150px);
  line-height: 1.17;
  letter-spacing: 0.04em;
}

h2,
.title {
  margin: 0 auto 2.5rem;
  font-weight: var(--fw-700);
  font-size: clamp(30px, 1.0057rem + 4.0909cqi, 75px);
  line-height: 1.5;
}

h3 {
  font-weight: var(--fw-600);
  font-size: clamp(20px, 1.1534rem + 0.4545cqi, 25px);
  line-height: 1.3;
}

strong {
  font-size: clamp(20px, 1.1534rem + 0.4545cqi, 25px);
  line-height: 1.2;
}

p {
  text-align: justify;
}

