@use "../utils/mixins"as *;
@use "../utils/functions"as *;

.leaders {
  &__list {
    margin-bottom: 2rem;
  }

  &__item {
    display: flex;
    gap: 1rem;

    &:not(:last-child) {
      margin-bottom: 2rem;
    }
  }

  &__descr {
    align-self: center;
    @include flex-col;

    gap: 16px;
  }

  &__title {
    margin: 0;
    font-size: clamp(20px, 1.0568rem + 0.9091vw, 30px);
  }

  &__position {
    font-weight: var(--fw-600);
    font-size: clamp(18px, 0.9898rem + 0.6364vw, 20px);
  }

  &__phone {

    @include flex-v-center;
    gap: 1rem;

    a {
      @include flex-all-center;
      gap: 10px;
      transition: color 0.3s linear;

      &::before {
        @include mask;

        mask-image: url("../images/icons/phone.svg");
        width: 24px;
        height: 24px;
      }

      &:hover {
        color: var(--color-orange-400);
      }
    }
  }
}