@import "https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100..900;1,100..900&display=swap";
:root {
  --content-width: 1060px;
  --container-offset: 16px;
  --container-width: calc(var(--content-width)  + (var(--container-offset) * 2));
  --font-family: "Raleway", sans-serif;
  --fw-600: 600;
  --fw-500: 500;
  --fw-700: 700;
  --fw-900: 900;
  --color-dark-900: #001322;
  --color-blue-400: #283642;
  --color-orange-400: #f15a25;
  --color-brown-400: #907f53;
  --color-blue-200: #2b94e4;
  --color-yellow-200: #fac904;
  --color-white: #fff;
  --color-gray-200: #808497;
  --color-gray-10: #f5f0f0;
  --color-gray-100: #7c7c7c;
  --color-red-600: #aa192a;
  color-scheme: dark;
  --radius-main: 5px;
  --radius-big: 15px;
  --radius-round: 50%;
}

.checkbox__field:checked + .checkbox__content:after {
  opacity: 1;
}

.checkbox__field:focus + .checkbox__content:before {
  outline: 1px solid var(--color-gray-100);
  outline-offset: 1px;
}

.checkbox__field:disabled + .checkbox__content {
  opacity: .4;
  pointer-events: none;
}

html, body {
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
}

.page {
  height: var(--vh);
  height: 100dvh;
  font-family: var(--font-family);
  font-weight: var(--fw-500);
  font-variant-numeric: lining-nums proportional-nums;
  letter-spacing: .03em;
  color: var(--color-dark-900);
  background-color: var(--color-white);
  font-optical-sizing: auto;
  font-size: clamp(16px, 1vw, 20px);
  font-style: normal;
  line-height: 1.5;
}

.page__body {
  min-width: 320px;
  min-height: var(--vh);
  position: relative;
}

.page__body:before {
  content: "";
  z-index: -1;
  opacity: 0;
  background-color: #27282bbf;
  transition: opacity .3s;
  position: absolute;
  inset: 0;
}

.page.open {
  overflow: hidden;
}

.page.open > .page__body:before {
  z-index: 5;
  opacity: 1;
  height: calc(var(--vh, 1vh) * 100);
}

.page__wrapper {
  gap: 2rem;
  display: flex;
}

.main {
  flex-basis: 100%;
}

.container, .wrapper {
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
}

.wrapper {
  max-width: 1440px;
}

.site-container {
  min-height: var(--vh);
  grid-template-rows: 1fr auto;
  grid-template-columns: 100%;
  display: grid;
  overflow-x: hidden;
}

.logo {
  width: 100%;
  max-width: 140px;
  display: block;
}

.logo img {
  width: 100%;
}

@media only screen and (max-width: 992px) {
  .logo {
    max-width: 70px;
    height: 70px;
  }
}

.section {
  margin-bottom: 5rem;
}

.section__content {
  margin-bottom: 2rem;
}

.section__content p:not(:last-child) {
  margin-bottom: 20px;
}

.section__content strong {
  margin-bottom: 1.5rem;
  display: block;
}

.section__content ul {
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1.5rem;
  display: flex;
}

.section__content li:before {
  content: "-";
  color: inherit;
}

.section__content a {
  text-decoration: underline;
  transition: color .3s;
}

.section__content a:hover, .section__content a:focus {
  color: var(--color-blue-200);
}

@media only screen and (max-width: 992px) {
  .section__content {
    margin-bottom: 50px;
  }
}

@media only screen and (max-width: 576px) {
  .section__content {
    margin-bottom: 35px;
  }
}

.aside {
  width: 100%;
  max-width: 270px;
  box-shadow: 0 0 4px var(--color-dark-900);
  position: relative;
}

.aside .nav {
  height: auto;
  padding: 0;
}

.aside .nav__list, .aside .sub-menu {
  width: 100%;
  flex-direction: column;
  margin: 0;
  display: flex;
}

.aside .nav__list li, .aside .sub-menu li {
  width: 100%;
  border-bottom: 1px solid var(--color-dark-900);
  position: relative;
  margin: 0 !important;
}

.aside .nav__list a, .aside .sub-menu a {
  justify-content: space-between;
  padding: 10px;
  transition-property: color, background-color;
  transition-duration: .3s;
  transition-timing-function: linear;
  display: flex;
}

.aside .nav__list a:before, .aside .sub-menu a:before {
  content: "";
  z-index: -1;
  width: 0;
  height: 100%;
  background-color: var(--color-dark-900);
  transition: width .2s;
  position: absolute;
  top: 0;
  left: 0;
}

.aside .nav__list a:hover, .aside .nav__list a:focus, .aside .sub-menu a:hover, .aside .sub-menu a:focus {
  color: var(--color-white);
}

.aside .nav__list a:hover:before, .aside .nav__list a:focus:before, .aside .sub-menu a:hover:before, .aside .sub-menu a:focus:before {
  width: 100%;
}

.map {
  height: 550px;
  position: relative;
}

.map iframe {
  width: 100%;
  display: block;
  position: absolute;
  inset: 0;
}

.energy a, .purchases a {
  font-weight: var(--fw-600);
  font-size: clamp(20px, 1.1534rem + .4545cqi, 25px);
}

.vacancies a, .single-research a {
  text-decoration: underline;
  transition: color .3s;
}

.vacancies a:hover, .vacancies a:focus, .single-research a:hover, .single-research a:focus {
  color: var(--color-blue-200);
}

.vacancies strong, .single-research strong {
  margin-bottom: 1.5rem;
}

.vacancies p:not(:last-child), .single-research p:not(:last-child) {
  margin-bottom: 20px;
}

.vacancies span, .single-research span {
  font-weight: var(--fw-600);
  margin-bottom: 1rem;
  font-size: clamp(20px, 1.1534rem + .4545cqi, 25px);
  line-height: 1.3;
  display: block;
}

.vacancies__list, .single-research__list {
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1.5rem;
  display: flex;
}

.vacancies__requirements, .single-research__requirements {
  counter-reset: li-counter;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1.5rem;
  display: flex;
}

.vacancies__requirements li, .single-research__requirements li {
  counter-increment: li-counter;
}

.vacancies__requirements li:before, .single-research__requirements li:before {
  content: counter(li-counter) ". ";
}

.payment span {
  font-weight: 700;
}

.research__list {
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1.5rem;
  display: flex;
}

.research__list li {
  gap: 1rem;
  display: flex;
}

.research__list li:not(:first-child) {
  counter-increment: li-counter;
}

.research__list li:not(:first-child):before {
  content: counter(li-counter) ". ";
}

.research__list li spam {
  display: block;
}

.single-research__img {
  margin-bottom: 1.5rem;
}

.single-research__img img {
  aspect-ratio: 16 / 9;
  width: 100%;
}

.scroll-to-top {
  width: 40px;
  height: 40px;
  background-color: var(--color-white);
  border: 1px solid var(--color-dark-900);
  z-index: 50;
  opacity: 0;
  visibility: hidden;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: opacity .3s, border-color .3s;
  display: flex;
  position: fixed;
  bottom: 20px;
  right: 20px;
  overflow: hidden;
  box-shadow: 0 2px 10px #0000004d;
}

.scroll-to-top:before {
  content: "";
  width: 12px;
  height: 8px;
  background-color: currentColor;
  transition: background-color .4s linear;
  display: block;
  position: relative;
  transform: rotate(-180deg);
  -webkit-mask-image: url("../images/icons/chevron.svg");
  mask-image: url("../images/icons/chevron.svg");
  -webkit-mask-position: center;
  mask-position: center;
  -webkit-mask-size: cover;
  mask-size: cover;
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
}

.scroll-to-top.visible {
  opacity: 1;
  visibility: visible;
}

.scroll-to-top:hover {
  opacity: .7;
}

.hidden {
  display: none !important;
}

.sr-only {
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  width: 1px !important;
  height: 1px !important;
  white-space: nowrap !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  border: 0 !important;
  margin: -1px !important;
  padding: 0 !important;
  position: absolute !important;
  overflow: hidden !important;
}

.dis-scroll {
  width: 100%;
  height: 100dvh;
  height: calc(var(--vh, 1vh) * 100);
  overscroll-behavior: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

h1, h2, h3 {
  width: -moz-fit-content;
  width: fit-content;
  letter-spacing: .03em;
  overflow: hidden;
}

h1 {
  font-weight: var(--fw-900);
  letter-spacing: .04em;
  font-size: clamp(45px, .6408rem + 7.2785vw, 150px);
  line-height: 1.17;
}

h2, .title {
  font-weight: var(--fw-700);
  margin: 0 auto 2.5rem;
  font-size: clamp(30px, 1.0057rem + 4.0909cqi, 75px);
  line-height: 1.5;
}

h3 {
  font-weight: var(--fw-600);
  font-size: clamp(20px, 1.1534rem + .4545cqi, 25px);
  line-height: 1.3;
}

strong {
  font-size: clamp(20px, 1.1534rem + .4545cqi, 25px);
  line-height: 1.2;
}

p {
  text-align: justify;
}

@keyframes toplinecross {
  0% {
    transform: translateY(var(--move-decrement));
  }

  50% {
    transform: translateY(0);
  }

  100% {
    transform: rotate(45deg);
  }
}

@keyframes bottomlinecross {
  0% {
    transform: translateY(var(--move));
  }

  50% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(0)rotate(-45deg);
  }
}

@keyframes toplineburger {
  0% {
    transform: rotate(45deg);
  }

  50% {
    transform: rotate(0);
  }

  100% {
    transform: translateY(var(--move-decrement)) rotate(0);
  }
}

@keyframes bottomlineburger {
  0% {
    transform: translateY(0)rotate(-45deg);
  }

  50% {
    transform: rotate(0);
  }

  100% {
    transform: translateY(var(--move)) rotate(0);
  }
}

@keyframes filter-animation {
  to {
    opacity: 0;
    min-height: 0;
    min-width: 0;
    border: 0;
    margin: 0;
    padding: 0;
  }
}

@keyframes slide-up {
  0% {
    transform: translateY(-3%);
  }

  100% {
    transform: translateY(0%);
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes slide-up-slow {
  0% {
    transform: translateY(-10%);
  }

  100% {
    transform: translateY(0%);
  }
}

@keyframes typewriter {
  to {
    left: 100%;
  }
}

@keyframes blink {
  to {
    background: none;
  }
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes grow {
  0% {
    max-height: var(--lineHeight);
  }

  100% {
    max-height: calc(var(--lineHeight) * var(--lines));
  }
}

@keyframes carriageReturn {
  0% {
    top: 0;
  }

  100% {
    top: calc(var(--lineHeight) * var(--lines));
  }
}

@keyframes type {
  0% {
    width: 100%;
  }

  100% {
    width: 0%;
  }
}

@keyframes caret {
  0% {
    color: var(--bgColor);
  }

  100% {
    color: currentColor;
  }
}

@keyframes progress {
  0% {
    stroke-dashoffset: -75px;
    opacity: 1;
  }

  95% {
    stroke-dashoffset: 0;
    opacity: 1;
  }

  100% {
    opacity: 0;
    stroke-dashoffset: 0;
  }
}

.header {
  z-index: 20;
  width: 100%;
  margin: 0 auto;
  transition: all .3s;
  position: relative;
  top: 0;
  left: 0;
  right: 0;
}

.header__wrapper {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header.show, .header .header__top {
  opacity: 1;
  transition: transform .3s, opacity .3s;
}

.header.scroll .header__bottom {
  z-index: 20;
  width: 100%;
  background-color: var(--color-blue-200);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 0 15px #0006;
}

.header.scroll .header__bottom .social {
  display: none;
}

.header .nav {
  background-color: var(--color-blue-200);
}

.header .social {
  width: 100%;
  max-width: 270px;
  background-color: var(--color-yellow-200);
  padding: 15px;
}

.header .social__link {
  border-color: var(--color-blue-200);
}

.header .social__link:before {
  background-color: var(--color-blue-200);
}

.header .social__link:hover:before {
  background-color: var(--color-dark-900);
}

.header__top {
  will-change: transform, opacity;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 20px;
  padding: 10px 0;
  transition: transform .3s, opacity .3s;
}

.header__top.hide {
  opacity: 0;
  transform: translateY(-100%);
}

.header__top.show {
  animation: .6s forwards fadeInDown;
}

.header__bottom {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.header__inner {
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.header__desc {
  color: var(--color-white);
  text-transform: uppercase;
  text-align: center;
  text-shadow: -1px 0 #001322, 0 -1px #001322, 1px 0 #001322, 0 1px #001322, -1px -1px #001322, 1px 1px #001322, -1px 1px #001322, 1px -1px #001322, 1px 0 4px #001322, 0 0 2px #001322, 0 0 3px #001322, 0 0 4px #001322;
  font-size: clamp(20px, .9602rem + 1.3636cqi, 35px);
  font-weight: 700;
}

@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translateY(-10%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.nav {
  height: 70px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 18px 0;
  display: flex;
  position: relative;
}

.nav__inner {
  width: 100%;
  align-items: center;
  display: flex;
}

.nav__list {
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.nav__list li:not(:last-child) {
  margin-right: clamp(20px, 2.61cqi, 50px);
}

.nav__list li .sub-menu {
  z-index: 10;
  box-shadow: 0 0 2px var(--color-dark-900);
  width: 305px;
  background-color: var(--color-white);
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s linear;
  display: none;
  position: absolute;
  top: 0;
  left: 100%;
}

.nav__list li .sub-menu li {
  margin-right: 0;
}

.nav__list li .sub-menu li:not(:last-child) {
  margin-bottom: 10px;
}

.nav__list li .sub-menu a:after {
  content: none;
}

.nav__list li:hover .sub-menu {
  opacity: 1;
  visibility: visible;
  flex-direction: column;
  display: flex;
}

.nav__list a {
  text-transform: uppercase;
  white-space: nowrap;
  font-size: 18px;
  transition: color .3s;
  position: relative;
}

.nav__list a:before {
  content: "";
  width: 100%;
  height: 2px;
  transform-origin: 0;
  background-color: currentColor;
  transition: transform .3s linear;
  position: absolute;
  bottom: -12px;
  left: 0;
  transform: scale(0);
}

.nav__list a:hover, .nav__list a.true {
  color: var(--color-yellow-200);
}

.nav__list a:hover:before, .nav__list a.true:before {
  transform: scale(1);
}

.nav__list .link-dropdown {
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.nav__list .link-dropdown:after {
  content: "";
  width: 12px;
  height: 8px;
  background-color: currentColor;
  transition: transform .3s linear;
  position: relative;
  rotate: -90deg;
  -webkit-mask-image: url("../images/icons/chevron.svg");
  mask-image: url("../images/icons/chevron.svg");
  -webkit-mask-position: center;
  mask-position: center;
  -webkit-mask-size: cover;
  mask-size: cover;
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
}

.nav__list .link-dropdown:has( + .sub-menu.show):after {
  transform: scaleY(1);
}

@media only screen and (max-width: 992px) {
  .nav__inner {
    right: calc(var(--container-offset) * -1);
    top: calc(var(--header-height) / 2.01);
    z-index: -1;
    width: 100%;
    height: calc(var(--vh)  - var(--header-height) * .4);
    max-width: 350px;
    background-color: var(--color-dark-900);
    flex-direction: column;
    justify-content: flex-start;
    padding: 40px 0;
    transition-property: transform;
    transition-duration: .25s;
    position: absolute;
    overflow-y: auto;
    transform: translateX(150%);
  }

  .nav__list {
    flex-direction: column;
    margin: 0;
  }

  .nav__list li:not(:last-child) {
    margin-right: 0;
  }

  .nav__list li .sub-menu {
    position: relative;
    left: 0;
  }

  .nav__list li .sub-menu.show {
    opacity: 1;
    visibility: visible;
    flex-direction: column;
    display: flex;
  }

  .nav__list .link-dropdown:has( + .sub-menu.show):after {
    rotate: -180deg;
  }

  .nav__link {
    font-size: 18px;
  }
}

[class].btn {
  z-index: 2;
  border-radius: var(--radius-main);
  width: 100%;
  max-width: 250px;
  font-weight: var(--fw-500);
  letter-spacing: .03em;
  white-space: nowrap;
  color: var(--color-white);
  background-color: var(--color-dark-900);
  border: 1px solid #0000;
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 19px 15px;
  font-size: 18px;
  line-height: 1.65;
  text-decoration: none;
  transition-property: color, background-color;
  transition-duration: .3s;
  transition-timing-function: linear;
  display: flex;
  position: relative;
  overflow: hidden;
}

[class].btn:before {
  content: "";
  z-index: -1;
  border-radius: var(--radius-main);
  width: 10%;
  background-color: var(--color-white);
  opacity: 0;
  transition: transform .3s linear, opacity .3s linear;
  position: absolute;
  inset: 0;
}

[class].btn:after {
  content: "";
  width: 27px;
  height: 27px;
  background-color: currentColor;
  transition: background-color .4s linear;
  position: relative;
  -webkit-mask-image: url("../images/icons/cursor.svg");
  mask-image: url("../images/icons/cursor.svg");
  -webkit-mask-position: center;
  mask-position: center;
  -webkit-mask-size: cover;
  mask-size: cover;
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
}

[class].btn:hover, [class].btn:focus {
  color: var(--color-dark-900);
}

[class].btn:hover:before, [class].btn:focus:before {
  opacity: 1;
  transform: scale(20, 1.1);
}

[class].btn--transparent {
  color: var(--color-white);
  background-color: #0000;
  border: 1px solid;
}

[class].btn--form {
  border-color: var(--color-gray-100);
  max-width: 100%;
  color: var(--color-gray-100);
  background-color: #0000;
}

[class].btn--corruption {
  max-width: 450px;
  text-transform: uppercase;
  color: var(--color-dark-900);
  background-color: var(--color-yellow-200);
}

[class].btn--pagination {
  max-width: 210px;
  color: var(--color-white);
  background-color: #0000;
  border-color: currentColor;
  padding: 20px;
}

[class].btn--pagination:after {
  width: 16px;
  height: 16px;
  -webkit-mask-image: url("../images/icons/arrow.svg");
  mask-image: url("../images/icons/arrow.svg");
}

@media only screen and (max-width: 576px) {
  [class].btn {
    max-width: 100%;
  }
}

.slider-btn {
  width: 50px;
  height: 50px;
  background-color: var(--color-white);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.slider-btn:before {
  content: "";
  width: 25px;
  height: 25px;
  background-color: currentColor;
  background-color: var(--color-dark-900);
  transition: background-color .4s linear;
  position: relative;
  -webkit-mask-image: url("../images/icons/arrow.svg");
  mask-image: url("../images/icons/arrow.svg");
  -webkit-mask-position: center;
  mask-position: center;
  -webkit-mask-size: cover;
  mask-size: cover;
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
}

.slider-btn:hover:not(.swiper-button-disabled):before {
  background-color: var(--color-gray-100);
}

.slider-btn--prev:before {
