@use "../utils/functions/"as *;
@use "../utils/mixins"as *;

.gallery {
  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;

    &:has(li:hover)>li:not(:hover) {
      opacity: 0.6;
    }

    li {
      width: 100%;
      transition: opacity 0.3s linear;
    }

    img {
      width: 100%;
    }
  }


}