@use "../utils/mixins"as *;
@use "../utils/functions"as *;

.history {


  h1+p {
    margin-bottom: 1.5rem;
    text-indent: 30px;

    span {
      font-size: 20px;
      font-weight: 900;
      font-style: italic;
    }
  }

  &__person {
    margin-bottom: 1.5rem;

    p {
      &:first-of-type {
        text-indent: 30px;
      }
    }

    + p {
      margin-bottom: 1.5rem;
    }
  }

  &__img {
    overflow: hidden;
    width: 250px;
    float: left;
    margin: 0 2rem 1rem 0;
  }

}