@use "../utils/functions/" as *;
@use "../utils/mixins" as *;

.article {
  @include flex-col;
  overflow: hidden;
  position: relative;
  margin-bottom: 20px;
  border-radius: var(--radius-main);
  border: 1px solid var(--color-dark-900);
  height: 100%;
  transition: box-shadow 0.3s linear;

  &:hover {
    box-shadow: inset 0 0 6px 0 var(--color-dark-900);

    & a {
      text-decoration-color: currentColor;
    }
  }

  img {
    overflow: hidden;
    min-height: 300px;
    margin-bottom: 20px;
    border-radius: var(--radius-main);
    width: 100%;
  }

  h3 {
    margin-bottom: 20px;
    font-size: clamp(18px, 1.0864rem + 0.1818cqi, 20px);
    font-weight: 700;
  }

  p {
    font-size: clamp(16px, 0.9614rem + 0.1818cqi, 18px);
  }

  a {
    margin-top: auto;
    text-decoration: 2px underline transparent;
    text-underline-offset: 5px;
    transition: text-decoration-color 0.3s linear;

    &::before {
      content: "";
      position: absolute;
      inset: 0;
      z-index: 5;
    }
  }

  &__desc {
    @include flex-col;
    align-items: center;
    height: 100%;
    padding: 15px;

    p {
      @include line-clamp(4);
    }
  }
}
