@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100..900;1,100..900&display=swap");


:root {
  --content-width: 1060px;
  --container-offset: 16px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // font-family
  --font-family: "Raleway", sans-serif;

  // font-weight
  --fw-600: 600;
  --fw-500: 500;
  --fw-700: 700;
  --fw-900: 900;

  // color
  --color-dark-900: #001322;
  --color-blue-400: #283642;
  --color-orange-400: #f15a25;
  --color-brown-400: #907f53;
  --color-blue-200: #2b94e4;
  --color-yellow-200: #fac904;
  --color-white: #fff;
  --color-gray-200: #808497;
  --color-gray-10: #f5f0f0;
  --color-gray-100: #7c7c7c;
  --color-red-600: #aa192a;
  color-scheme: dark;

  //  radius
  --radius-main: 5px;
  --radius-big: 15px;
  --radius-round: 50%;

  // shadow

  // gradient

  //filter
  // --filter-dark-900: brightness(0) saturate(100%) invert(8%) sepia(9%) saturate(4899%) hue-rotate(158deg) brightness(95%) contrast(103%);

}