@use "../../utils/functions" as *;
@use "../../utils/mixins" as *;

.banners {
  &__slider {
    position: relative;
    overflow: hidden;
  }

  &__item {
    @include cover-background;
    @include flex-all-center;

    min-height: 550px;
    border-image: linear-gradient(
      to bottom,
      hsl(0 0% 0% / 0.1),
      hsl(0 0% 0% / 0.2)
    ) fill 1;
  }

  &__title {
    font-size: clamp(30px, 1.4886rem + 1.8182vw, 50px);
    color: var(--color-white);
  }

  &__content {  
    
    .btn {
      margin-inline: auto;
    }

    .title {
      text-align: center;
    }
  }

  &__pagination {
    .swiper-pagination-bullet {
      background-color: transparent;
      opacity: 0.7;

      &--animation {
        display: inline-block;
        width: 20px;
        height: 20px;

        svg {
          rotate: -90deg;
        }

        .svg__circle-inner {
          fill: transparent;
          stroke: var(--color-white);
        }
      }

      &-active {
        opacity: 1;

        .svg__circle {
          stroke: var(--color-white);
          stroke-dasharray: 75;
          stroke-dashoffset: 0;
          animation: progress 2s ease-in-out 1 forwards;
        }
        .svg__circle-inner {
          fill: var(--color-white);
        }
      }
    }
  }

  .slider-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 16px;
    z-index: 5;

    &--prev {
      right: auto;
      left: 16px;
    }
  }
}
